.particles-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  pointer-events: none;
  z-index: 2;
}

.particle {
  position: absolute;
  background-color: rgb(0, 0, 0);
  border-radius: 50%;
  opacity: 0;
  pointer-events: none;
}
